import arthur from '../assets/img/avatars/arthur.jpg'
import aiysha from '../assets/img/avatars/aiysha.jpg'
import kady from '../assets/img/avatars/kady.jpg'
import doublequotes from '../assets/img/decoration/doublequotes.png'
import brcorner from '../assets/img/decoration/brcorner.jpg'
import tlcorner from '../assets/img/decoration/tlcorner.jpg'

export function Testimonials() {
    return (
        <section className="testimonials-main">
            <div className="testimonials-top-right">
                <img className="testimonials-top-right" alt="kady" src={tlcorner} />
            </div>

            <div className="testimonials-bottom-left">
                <img className="testimonials-bottom-left" alt="kady" src={brcorner} />
            </div>
            <div className="testimonials-headline">
                <p>Delivering real results for top companies. Some of our <span>success stories.</span></p>
            </div>
            <div className="testimonials-wrapper">
                <div className="testimonials-block" >
                    <div className="testimonials-doublequotes">
                        <img className="doublequotes" alt="kady" src={doublequotes} />
                    </div>
                    <div className="testimonials-opener">
                        <p> “The team perfectly fit the specialized skill set required.
                            They focused on the most essential features
                            helping us launch the platform eight months
                            faster than planned.”
                        </p>
                    </div>

                    <h4>
                        Kady Baker
                    </h4>

                    <p>Product Manager at Bookmark</p>

                    <div className="testimonials-avatar">
                        <img className="avatar-img" alt="kady" src={kady} />
                    </div>
                </div>
                <div className="testimonials-block" >
                    <div className="testimonials-doublequotes">
                        <img className="doublequotes" alt="kady" src={doublequotes} />
                    </div>
                    <div className="testimonials-opener">
                        <p>
                            “We needed to automate our entire onboarding process.
                            The team came in and built out the whole journey.
                            Since going live, user retention has gone through the roof!”
                        </p>
                    </div>

                    <h4>
                        Aiysha Reese
                    </h4>

                    <p>Founder of Manage</p>

                    <div className="testimonials-avatar">
                        <img className="avatar-img" alt="aiysha" src={aiysha} />
                    </div>
                </div>
                <div className="testimonials-block" >
                    <div className="testimonials-doublequotes">
                        <img className="doublequotes" alt="kady" src={doublequotes} />
                    </div>
                    <div className="testimonials-opener">
                        <p>
                            “Amazing. Our team helped us build an app that delivered a new
                            experience for hiring a physio.
                            The launch was an instant success with
                            100k downloads in the first month.”
                        </p>
                    </div>

                    <h4>
                        Arthur Clarke
                    </h4>

                    <p>Co-founder of MyPhysio</p>

                    <div className="testimonials-avatar">
                        <img className="avatar-img" alt="arthur" src={arthur} />
                    </div>
                </div>
            </div>
        </section>

    )
}

